<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        filterable
        clearable
        style="width: 450px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
      <el-button
        class="filter-item"
        :disabled="!instanceValue"
        type="primary"
        icon="search"
        @click="connection"
        size="small"
        >获取告警信息</el-button
      >
    </template>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>上下行吞吐量性能上报</span>
        <el-button
          style="float: right; padding: 3px 0"
          type="text"
          @click="setClick"
          >性能告警配置</el-button
        >
      </div>
      <p>traffic_ul_rx_packs:{{ perform.traffic_ul_rx_packs }}</p>
      <p>traffic_ul_rx_bytes:{{ perform.traffic_ul_rx_bytes }}</p>
      <p>traffic_ul_tx_packs:{{ perform.traffic_dl_tx_packs }}</p>
      <p>traffic_ul_tx_bytes:{{ perform.traffic_ul_tx_bytes }}</p>
      <p>traffic_ul_drop_packs:{{ perform.traffic_ul_drop_packs }}</p>
      <p>traffic_ul_drop_bytes:{{ perform.traffic_ul_drop_bytes }}</p>
      <p>traffic_dl_rx_packs:{{ perform.traffic_dl_rx_packs }}</p>
      <p>traffic_dl_rx_bytes:{{ perform.traffic_dl_rx_bytes }}</p>
      <p>traffic_dl_tx_packs:{{ perform.traffic_dl_tx_packs }}</p>
      <p>traffic_dl_tx_bytes:{{ perform.traffic_dl_tx_bytes }}</p>
      <p>traffic_dl_drop_packs:{{ perform.traffic_dl_drop_packs }}</p>
      <p>traffic_dl_drop_bytes:{{ perform.traffic_dl_drop_packs }}</p>
    </el-card>

    <el-dialog
      title="性能上报配置"
      :visible.sync="dialogVisible"
      width="600px"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="form" label-width="130px">
        <el-form-item label="Respot Switch">
          <el-switch
            v-model="form.respot_switch"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
        <el-form-item label="Report Interval">
          <el-input v-model="form.report_interval"></el-input>
        </el-form-item>
        <el-form-item label="Report Permanent">
          <el-switch
            v-model="form.report_permanent"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitConfig">确 定</el-button>
      </div>
    </el-dialog>
  </d2-container>
</template>

<script>
import SockJS from "sockjs-client";
import Stomp from "stompjs";
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetPerformConfig,
  fetchSetPerformConfig,
} from "@/api/demo/index";
export default {
  name: "alarm",
  data() {
    return {
      serviceName: "",
      serviceOptions: [],
      stompClient3: null,
      perform: {},
      dialogVisible: false,
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      form: {
        report_switch: false,
        report_interval: null,
        report_permanent: false,
      },
    };
  },
  mounted() {
    this.getService();
    this.connection();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    //获取当前upf下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择实例
    // changeInstanc(val) {
    //   if (!val) {
    //     return;
    //   }
    //   this.connection();
    // },
    setClick() {
      this.dialogVisible = true;
      this.getPerformConfig();
    },
    getPerformConfig() {
      fetchGetPerformConfig({
        request_type: "perform config get",
        instance_id: this.instanceValue,
        sequence_no: 4001,
      })
        .then((response) => {
          this.form = response;
          clearInterval(this.timer);
          this.timer = null;
          this.setTimer();
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    submitConfig() {
      this.dialogVisible = false;
      fetchSetPerformConfig({
        request_type: "perform config set",
        instance_id: this.instanceValue,
        sequence_no: 4002,
        data: this.form,
      })
        .then((response) => {})
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    handleClose() {
      this.dialogVisible = false;
    },
    connection() {
      // 建立连接对象
      let socket3 = new SockJS("http://holer.cc:50469/alarm-info");
      // 获取STOMP子协议的客户端对象
      this.stompClient3 = Stomp.over(socket3);
      // 定义客户端的认证信息,按需求配置
      let headers = {
        Authorization: "",
      };
      this.stompClient3.connect(
        headers,
        () => {
          this.stompClient3.subscribe(
            "/alarm/perform",
            (val) => {
              console.log(val);
              this.perform = JSON.parse(val.body);
            },
            headers
          );
        },
        (err) => {
          // 连接发生错误时的处理函数
          console.log("失败");
          console.log(err);
        }
      );
    }, //连接 后台
    disconnect() {
      if (this.stompClient) {
        this.stompClient.disconnect();
      }
    }, // 断开连接
  },
  beforeDestroy: function () {
    // 页面离开时断开连接,清除定时器
    this.disconnect();
    //   clearInterval(this.timer);
  },
};
</script>
